<template>
  <v-card color="#fff" max-width="374">
    <v-card-media>
      <template>
        <v-carousel hide-delimiters
                    height="250"
                    show-arrows-on-hover
        >
          <v-carousel-item
            v-for="image in images"
            :src="image"
          ></v-carousel-item>
        </v-carousel>
      </template>
    </v-card-media>
    <v-card-title>{{ name }}</v-card-title>
    <v-card-subtitle>{{ price }}$</v-card-subtitle>
    <v-card-text>{{ description }}</v-card-text>
    <v-card-actions v-if="showBuyButtons">
      <v-spacer></v-spacer>
      <v-alert
        v-if="isBought"
        dense
        text
        color="info"
      >
        You've already bought it
      </v-alert>
      <v-alert
        v-if="!isBought && !isEnoughMoney"
        dense
        text
        color="warning"
      >
        Not enough money
      </v-alert>
      <v-dialog
        v-model="confirmBuy"
        v-if="!isBought && isEnoughMoney"
        width="500"
      >
        <template v-slot:activator="{ on, attrs }">
          <v-btn
            color="primary lighten-2"
            text
            v-bind="attrs"
            v-on="on"
          >
            Buy
          </v-btn>
        </template>

        <v-card class="pt-2">
          <v-card-title>
            Confirmation
          </v-card-title>
          <v-card-text>
            Do you want to buy '{{ name }}' for {{ price }}$? Your balance is {{ balance }}$.
          </v-card-text>

          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
              color="primary"
              @click="buy(); confirmBuy = false"
            >
              Yes
            </v-btn>
            <v-btn
              color="primary"
              text
              @click="confirmBuy = false"
            >
              Cancel
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-card-actions>
  </v-card>
</template>

<script>
export default {
  name: "StoreItem",
  props: ["name", "description", "price", "images", "showBuyButtons"],
  data: () => ({
    confirmBuy: false
  }),
  computed: {
    balance() {
      return this.$store.getters.getBalance
    },
    isBought() {
      let purchases = this.$store.getters.getPurchases;
      if (!purchases) return false

      return purchases.find(p => p === this.name) != null
    },
    isEnoughMoney() {
      return this.balance >= this.price
    }
  },
  methods: {
    buy() {
      this.$store.commit('addPurchase', {
        itemName: this.name, price: this.price
      })
    }
  }
}
</script>

