<template>
  <v-app id="inspire">
    <v-content>
      <v-container fluid fill-height>
        <v-layout align-center justify-center>
          <v-flex xs12 sm8 md4>
            <v-card class="elevation-12">
              <v-toolbar dark color="primary">
                <v-toolbar-title>Sign Up</v-toolbar-title>
              </v-toolbar>
              <v-card-text>
                <v-form ref='form'>
                  <v-text-field
                    v-model="email"
                    :error-messages="emailErrors"
                    label="Email"
                    type="email"
                    required
                    v-on:change='sameEmailExists = false'
                  ></v-text-field>
                  <v-text-field
                    v-model="password"
                    :error-messages="passwordErrors"
                    :counter="32"
                    label="Password"
                    type="password"
                    required
                  ></v-text-field>
                  <v-text-field
                    v-model='repeatPassword'
                    :error-messages="repeatPasswordErrors"
                    :counter="32"
                    label="Repeat password"
                    type="password"
                  ></v-text-field>
                </v-form>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="primary" @click='submit'>Sign Up</v-btn>
              </v-card-actions>
            </v-card>
          </v-flex>
        </v-layout>
      </v-container>
    </v-content>
  </v-app>
</template>

<script>
import {required, maxLength, minLength, email, sameAs} from 'vuelidate/lib/validators'
import {registerUser} from '@/services/users';
import router from '@/router';

export default {
  name: 'SignUpForm',
  data: () => ({
    email: '',
    password: '',
    repeatPassword: '',
    sameEmailExists: false
  }),
  validations: {
    email: {required, email},
    password: {required, maxLength: maxLength(32), minLength: minLength(7)},
    repeatPassword: {required, sameAsPassword: sameAs('password')}
  },
  computed: {
    emailErrors() {
      const errors = []
      if (!this.$v.email.$dirty) return errors
      !this.$v.email.email && errors.push('Must be valid e-mail')
      !this.$v.email.required && errors.push('E-mail is required')
      this.sameEmailExists && errors.push('User with same email already exists')
      return errors
    },
    passwordErrors() {
      const errors = []
      if (!this.$v.password.$dirty) return errors
      !this.$v.password.maxLength && errors.push('Password must be at most 32 characters long')
      !this.$v.password.minLength && errors.push('Password must be at least 8 characters long')
      !this.$v.password.required && errors.push('Password is required')
      return errors
    },
    repeatPasswordErrors() {
      const errors = []
      if (!this.$v.repeatPassword.$dirty) return errors
      !this.$v.repeatPassword.required && errors.push('Password is required')
      !this.$v.repeatPassword.sameAsPassword && errors.push('Passwords must be identical')
      return errors
    },
  },

  methods: {
    submit() {
      this.$v.$touch()
      if (this.$v.$invalid) return

      try {
        registerUser({
          email: this.email,
          password: this.password
        })

        router.push('/')
      } catch (e) {
        this.sameEmailExists = true
      }
    }
  }
};
</script>

