<template>
  <div>
    <v-card
      :loading="loading"
      class="mx-auto my-12"
      elevation='2'
      max-width="374"
    >
      <v-img
        height="250"
        :src="imageSrc"
      ></v-img>

      <v-card-title>Question {{ price }}$</v-card-title>
      <v-card-text>
        {{ question }}
      </v-card-text>
      <v-card-text>
        <v-radio-group
          v-model="selectedAnswer"
        >
          <v-radio v-for='(option, i) in answers'
                   :label='option'
                   color="primary"
                   :value="i"
          ></v-radio>
        </v-radio-group>
      </v-card-text>
      <v-card-actions>
        <v-btn
          color="primary lighten-2"
          :disabled='selectedAnswer === null'
          @click="submit"
        >
          Answer
        </v-btn>
        <v-btn
          color="primary lighten-2"
          text
          @click="nextQuestion"
        >
          Skip
        </v-btn>
      </v-card-actions>
    </v-card>
    <v-snackbar
      v-model="showCorrectAnswerNotification"
      timeout="2000"
      color='green'
    >
      Correct Answer. You earned {{ lastOperationPrice }}$!

      <template v-slot:action="{ attrs }">
        <v-btn
          color="white"
          text
          v-bind="attrs"
          @click="showCorrectAnswerNotification = false"
        >
          Close
        </v-btn>
      </template>
    </v-snackbar>
    <v-snackbar
      v-model="showWrongAnswerNotification"
      timeout="2000"
      color='red'
    >
      Wrong Answer. You lost {{ lastOperationPrice }}$ :(

      <template v-slot:action="{ attrs }">
        <v-btn
          color="white"
          text
          v-bind="attrs"
          @click="showWrongAnswerNotification = false"
        >
          Close
        </v-btn>
      </template>
    </v-snackbar>
  </div>
</template>

<script>
import {logoutUser} from '@/services/users';
import router from '@/router';

export default {
  name: 'QuestionCard',
  data: () => ({
    loading: false,
    question: null,
    answers: null,
    correctAnswerIndex: null,
    price: null,
    imageSrc: null,
    selectedAnswer: null,
    // ui
    showCorrectAnswerNotification: false,
    showWrongAnswerNotification: false,
    lastOperationPrice: null
  }),
  methods: {
    async loadQuestion() {
      this.loading = true
      this.selectedAnswer = null
      let thiz = this
      fetch('https://opentdb.com/api.php?amount=1&type=multiple&encode=base64')
        .then(data => data.json())
        .then(res => {
          if (res.response_code !== 0 || res.results == null || res.results.length === 0) {
            console.debug('Received error from opentdb')
            return
          }
          let data = res.results[0]
          thiz.question = atob(data.question)

          let dif = atob(data.difficulty)
          if (dif === "easy") thiz.price = 1
          else if (dif === "medium") thiz.price = 3
          else if (dif === "hard") thiz.price = 5

          thiz.imageSrc = 'https://picsum.photos/350/250?random=' + new Date().getTime() // to make vue js reload the pic

          let correctAnswerVal = atob(data.correct_answer)
          thiz.answers = data.incorrect_answers
            .map(v => atob(v))
            .concat(correctAnswerVal)
            .sort((a, b) => a.localeCompare(b))
          thiz.correctAnswerIndex = thiz.answers.findIndex(a => a === correctAnswerVal)

          console.debug('Received new question from opentdb')

        })
        .then(_ => thiz.loading = false)

    },
    submit() {
      this.lastOperationPrice = this.price
      if (this.selectedAnswer === this.correctAnswerIndex) {
        this.showCorrectAnswerNotification = true
        this.$store.commit('changeBalance', this.price)
      } else {
        this.showWrongAnswerNotification = true
        this.$store.commit('changeBalance', -1 * this.price)
      }
      this.loadQuestion()
    },
    nextQuestion() {
      this.loadQuestion()
    }
  },
  created() {
    this.loadQuestion()
  }
};
</script>

