const USERS_REGISTRY_KEY = "users"
const LOGGED_IN_USER_KEY = "loggedInUser"

export function registerUser(user) {

  let usersStr = window.localStorage.getItem(USERS_REGISTRY_KEY);
  let users = []
  if (usersStr != null) {
    users = JSON.parse(usersStr)
  }

  let existingUser = users.find(v => v.email === user.email)
  if (existingUser) throw "User with same email already exists"

  users.push({
    email: user.email,
    password: user.password,
    balance: 0,
    purchases: []
  })

  window.localStorage.setItem(USERS_REGISTRY_KEY, JSON.stringify(users))
  console.info("New user is registered: " + user.email)
}

export function loginUser(email, password) {

  let usersStr = window.localStorage.getItem(USERS_REGISTRY_KEY);
  let users = []
  if (usersStr != null) {
    users = JSON.parse(usersStr)
  }

  let foundUser = users.find(v => v.email === email && v.password === password);
  if (foundUser)
    window.localStorage.setItem(LOGGED_IN_USER_KEY, JSON.stringify(foundUser))

  return foundUser
}


export function getLoggedUser() {

  return JSON.parse(window.localStorage.getItem(LOGGED_IN_USER_KEY))
}

export function logoutUser() {

  window.localStorage.removeItem(LOGGED_IN_USER_KEY)
}

/**
 * returns current balance
 */
export function changeBalance(delta) {
  let user = updateUser(user => {
    user.balance += delta
    return user
  });

  return user.balance
}

export function addPurchase(itemName, price) {
  updateUser(user => {
    if (!user.purchases) user.purchases = []

    if (price >= 0 && user.balance < price) {
      throw "not enough money"
    }

    user.balance -= price
    user.purchases.push(itemName)

    return user
  });
}

/**
 *
 * @param action is a function with user first arg and use returned user
 */
function updateUser(action) {

  let user = JSON.parse(window.localStorage.getItem(LOGGED_IN_USER_KEY))
  if (!user) {
    console.error("No logged user found")
    throw "No user found"
  }
  user = action(user)

  window.localStorage.setItem(LOGGED_IN_USER_KEY, JSON.stringify(user))
  updateUserInRegistry(user)

  return user
}

function updateUserInRegistry(user) {

  let usersStr = window.localStorage.getItem(USERS_REGISTRY_KEY);
  let users = []
  if (usersStr != null) {
    users = JSON.parse(usersStr)
  }

  let userIndex = users.findIndex(v => v.email === user.email);

  if (userIndex !== -1) {
    users[userIndex] = user
  } else {
    console.error("Can't update user balance, it is not found in local storage: " + user.email)
    users.push(user)
  }

  window.localStorage.setItem(USERS_REGISTRY_KEY, JSON.stringify(users))
}
