<template>
  <v-card
    elevation="2"
    width=250
  >
    <v-img v-if='showImage == "true"'
           :src="imageSrc"
    ></v-img>
    <v-card-title>Your balance is {{ balance }}$
    </v-card-title>
  </v-card>
</template>

<script>
import {logoutUser} from '@/services/users';
import router from '@/router';

export default {
  name: 'BalanceBar',
  props: ['showImage'],
  data: () => ({}),
  computed: {
    balance() {
      return this.$store.getters.getBalance || 0
    },
    imageSrc() {
      let balance = this.$store.getters.getBalance
      if (balance > 100)
        return 'https://www.thebillfold.com/wp-content/uploads/2012/04/0pU-08JZteEMWVMsX.jpg'
      else if (balance > 10)
        return 'https://www.scaryforkids.com/pics/gold-coins.jpg'
      else if (balance > 0)
        return 'https://thumbs.dreamstime.com/b/few-euro-coins-light-background-concept-savings-150235919.jpg'
      else if (balance === 0)
        return 'https://static.vecteezy.com/system/resources/thumbnails/001/377/259/small/table-top-with-empty-background-free-photo.jpg'
      else if (balance < 0)
        return 'https://t3.ftcdn.net/jpg/01/99/96/26/360_F_199962658_YMlPCh0Rh9Wc2CunL6mXjZrBoBJIQFKo.jpg'
    }
  }
};
</script>

