import Vue from 'vue';
import Vuex from 'vuex';
import {addPurchase, changeBalance, getLoggedUser, loginUser, logoutUser} from '@/services/users';

Vue.use(Vuex);

// todo optimize
export default new Vuex.Store({
  state: {
    loggedUser: getLoggedUser()
  },
  getters: {
    getPurchases(state) {
      return state.loggedUser.purchases
    },
    getEmail(state) {
      return state.loggedUser.email
    },
    getBalance(state) {
      return state.loggedUser.balance
    },
  },
  mutations: {
    login(state, user) {
      state.loggedUser = user
    },
    logout(state) {
      logoutUser()
      state.loggedUser = null
    },
    changeBalance(state, delta) {
      changeBalance(delta)
      state.loggedUser = getLoggedUser()
    },
    addPurchase(state, {itemName, price}) {
      addPurchase(itemName, price)
      state.loggedUser = getLoggedUser()
    },
  },
  actions: {},
  modules: {},
});
