<template>
  <v-app id="inspire">
    <v-content>
      <v-container fluid fill-height>
        <v-layout align-center justify-center>
          <v-flex xs12 sm8 md4>
            <v-card class="elevation-12">
              <v-toolbar dark color="primary">
                <v-toolbar-title>Sign In</v-toolbar-title>
              </v-toolbar>
              <v-card-text>
                <v-form>
                  <v-text-field
                    prepend-icon="mdi-account"
                    v-model="email"
                    label="Email"
                    type="email"
                  ></v-text-field>
                  <v-text-field
                    prepend-icon="mdi-lock"
                    v-model="password"
                    label="Password"
                    type="password"
                  ></v-text-field>
                </v-form>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="primary" @click="submit">Sign In</v-btn>
              </v-card-actions>
            </v-card>
          </v-flex>
        </v-layout>
      </v-container>

      <v-snackbar
        v-model="showWrongCredsNotification"
        timeout="2000"
        color="red"
      >
        Incorrect credentials
      </v-snackbar>
    </v-content>
  </v-app>
</template>

<script>
import {loginUser, registerUser} from '@/services/users';
import router from '@/router';

export default {
  name: 'SignInForm',
  data: () => ({
    email: '',
    password: '',
    // ui
    showWrongCredsNotification: false
  }),
  methods: {
    submit() {
      let foundUser = loginUser(this.email, this.password)
      if (!foundUser) {
        this.showWrongCredsNotification = true
      } else {
        this.$store.commit('login', foundUser)
        router.push('/')
      }
    }
  }
}
</script>

