<template>
  <div>
    <v-card
      class="d-flex align-center mb-6"

      :color="$vuetify.theme.dark ? 'grey darken-3' : 'grey lighten-4'"
      flat
      tile
    >
      <v-chip
        class="ma-2"
        color="primary"

      >
        Your balance is {{ balance }}$
      </v-chip>
      <v-switch
        v-model="showNewAndBought"
        inset
        :label="showNewAndBought ? `Show all items` : `Show only not bought yet items`"
        class="ml-4"
      ></v-switch>
      <v-switch
        v-model="showAllPrice"
        inset
        :label="showAllPrice ? `Show all prices` : `Show only affordable prices`"
        class="ml-4"
      ></v-switch>
    </v-card>
    <v-container fluid grid-list-xl class="mt-4">
      <v-layout wrap justify-space-around>
        <v-flex v-for="item in items">
          <StoreItem
            :name="item.name"
            :description="item.description"
            :price="item.price"
            :images="item.images"
            show-buy-buttons="true"
          />
        </v-flex>
        <v-flex v-for="image in items" class="flex-empty">
          <div></div>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
import StoreItem from '@/components/StoreItem';
import {getAllStoreItems} from '@/services/store';

export default {
  name: "StoreComp",
  components: {StoreItem},
  data: () => ({
    showNewAndBought: true,
    showAllPrice: true
  }),
  computed: {
    items() {
      let purchases = this.$store.getters.getPurchases
      let balance = this.$store.getters.getBalance

      let itemsToShow = getAllStoreItems()
      if (!this.showNewAndBought) {
        itemsToShow = itemsToShow.filter(item => !purchases.includes(item.name))
      }
      if (!this.showAllPrice) {
        itemsToShow = itemsToShow.filter(item => item.price <= balance)
      }

      return itemsToShow
    },
    balance() {
      return this.$store.getters.getBalance
    }
  }
}
</script>

<style scoped>
.flex {
  flex-grow: 0;
}

.flex-empty {
  height: 0 !important;
  padding-top: 0 !important;
  padding-bottom: 0 !important;
}
</style>
