import Vue from 'vue';
import VueRouter from 'vue-router';
import SignIn from '../views/SignIn.vue';
import Home from '@/views/Home.vue';
import SignUp from '@/views/SignUp.vue';
import Earn from '@/views/Earn';
import Overview from '@/views/Overview';
import Store from '@/views/Store';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
  },
  {
    path: '/sign-in',
    name: 'SignIn',
    component: SignIn,
  },
  {
    path: '/sign-out',
    name: 'SignOut',
    component: SignIn,
  },
  {
    path: '/sign-up',
    name: 'SignUp',
    component: SignUp,
  },
  {
    path: '/earn',
    name: 'Earn',
    component: Earn,
  },
  {
    path: '/overview',
    name: 'Overview',
    component: Overview,
  },
  {
    path: '/store',
    name: 'Store',
    component: Store,
  },
];

const router = new VueRouter({
  routes,
});

export default router;
